#bodyBox::after {
  content: "";
  height: 100vh;
  width: 100vw;
  background-color: rgba(0, 0, 0, 0.86);
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.15), 0 8px 10px rgba(0, 0, 0, 0.15),
    0 10px 12px rgba(0, 0, 0, 0.15), 0 14px 16px rgba(0, 0, 0, 0.15),
    0 18px 32px rgba(0, 0, 0, 0.15), 0 32px 64px rgba(0, 0, 0, 0.15);
  border-radius: 0rem;
  position: absolute;
  margin-top: -0.66rem;
  z-index: -3;
}

.icons {
  pointer-events: none;
}

@keyframes fadeInBody {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes headerOneFlight {
  0% {
    transform: translateX(200rem);
  }
  100% {
    transform: translateX(0rem);
  }
}


